.container{
  display: flex;
  justify-content: center;
  margin-top: 10% !important;
  width: 70%;
  margin: 0px 13%;
}
.form-data {
  width: 70%;
  margin:0px 15%;
}
.div {
  display: flex;
  flex-direction: column;
  margin-top: 5px;
}
#email,#password,#confirm-password{
  font-size: 18px;
  border-radius: 10px;
  padding: 8px;
  width:100%;
}
.submitBtn{
  background-color: rgb(0, 195, 255);
  color: white;
  height: 30px;
  width: 200px;
  font-size: 18px;
  /* padding: 5px; */
  border: 2px solid red;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}
label{
  font-size: 18px;
  font-weight: 500;
}
.error{
  color:red;
}